const Timeline = () => {
  const events = [{ year: 2022, event: "Started working at Avara (fka Aave)" },
    {year: 2019, event: "" }
  ];

  return (
    <section>
      <h2>Timeline</h2>
      <ul>
        {events.map((event, index) => (
          <li key={index}>
            <strong>{event.year}</strong>: {event.event}
          </li>
        ))}
      </ul>
    </section>
  );
};

export default Timeline;
